<template>
  <full-height-layout>

    <div slot="layout_title">报价单</div>

    <prices-view-top slot="layout_inner_head"></prices-view-top>

    <!-- 表格主体 -->
      <prices-view-body
        :prices="pricesList" 
        @pdfView_Father="pdfView" 
        @option_Father="option" 
        slot="layout_inner_body"

        v-if="!isLoading"
      ></prices-view-body>

      <transition v-else name="fade" slot="layout_inner_body">
        <loading></loading>
      </transition>


    <!-- 其它插件 -->
    <div slot="layout_extra">

      <trash @trashItem="_trash"></trash>


      <prices-pdf-modal 

        :the_member="null" 
        :the_prices_timed="null" 
        :the_prices_numed="null"
        :the_prices_content="[]"
        
        :isPrint="true"
        ref="pdfModal"
      >
      </prices-pdf-modal>
      
    </div>

  </full-height-layout>
</template>

<script>
import view from '@/common/es/view'
import config from '@/common/config'
import tool from '@/common/es/tool'
import conn from '@/service/conn'

import FullHeightLayout from '@/components/Layout/FullHeightLayout.vue'
import PricesViewTop from './Top/PricesViewTop.vue'

import PricesViewBody from './Body/PricesViewBody.vue'

import Trash from '@/components/Modal/Trash.vue'
import Loading from '@/components/Shimmer/Loading.vue'

import PricesPdfModal from '@/components/Modal/Pdf/PricesPdfModal.vue'

export default {
  components: { FullHeightLayout, PricesViewTop, PricesViewBody, Loading, Trash, PricesPdfModal },
  name: 'PricesView',
  data() {
    return {
      pricesList: [],
      activeId: 0,

      activePrices: {},

      isLoading: true
    }
  },
  mounted() {

    this.loadData()
  },
  methods: {
    loadData() {
      setTimeout(() => {
        conn.prices.getList_Prices({}, true).then(res => {
          this.isLoading = false
          this.pricesList = res
        })
      }, config.delayTime)
    },
    // 表格操作
    ser_timed(v) { return tool.timed.ser_timed(v, true, true) },
    option(id, option) {
      this.activeId = id

      if (option == 'update') {
        conn.prices_content.getOneByPrices({
          price_collect: id
        }).then(res => {
          
          if (conn.judge_response_list(res)) {
            
            this.$router.push({
              path: '/home/prices_update',
              query: {
                priceContentId: res[0].id
              }
            })
            
          } else {
            tool.showToast('该数据已过期！！！')
          }
        }).catch(err => { tool.showToast('网络连接失败。') })
      } else if (option == 'trash') {
        tool.showModal('trashModal')
      }
    },
    pdfView(id) {
      this.activePrices = this.pricesList.filter((e) => e.id == id)[0]

      this.$refs.pdfModal.collectData(this.activePrices)
      tool.showModal('pricesPdfViewModal')
    },
    // 数据操作
    _trash() {
      const _this = this
      tool.closeModal('trashModal')

      if ((this.activeId != null) && (this.activeId > 0)) {

        conn.prices.trash(this.activeId).then(res => {
          if (conn.judge_response(res)) {

            const id = res['id']
            tool.showToast('删除成功！！')
            _this.isLoading = true
            _this.loadData()
              
          }
        })

      }
    }
  }
}
</script>

<style lang="sass" scoped>
@import "@/common/sass/base.sass"
@import "@/common/sass/listing.sass"

</style>


